import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';

@observer
class Team extends Component {
    render() {
        const { team } = this.props;
        const { teamStore, userStore } = this.props.stores;

        const currentMonth = new Date().getMonth() + 1;
        const currentYear = new Date().getFullYear();
		const { month = currentMonth, year = currentYear } = this.props;
		const seasonStart = parseInt(month < 8 ? year - 1 : year, 10);

        const workoutSummary = teamStore.findWorkoutSummary(team.id, seasonStart) || {};
        const { user } = userStore;
        const { language = 'no' } = user;
        const teamAmbition = userStore.findTeamAmbition({ ambition: team.ambitions, language });
        const teamType = userStore.findTeamType({ type: team.type, language });
        const image = team.image && team.image.s3SmallLink ? team.image : null;

        return (
            <div class='w-100 mb-3'>
                <div class='row'>
                    <div class='col-12 col-sm-8 col-lg-6 offset-sm-2 offset-lg-3 d-flex mb-0 position-relative'>
                        {/* {JSON.stringify(team, null, 4)} */}
                        <div class={`text-center border rounded-circle imageRounded`} style={image ? `background-image: url("${image.s3SmallLink}");` : ''}>
                            {!image && <i class='fas fa-users text-muted mt-3' style='font-size: 40px;' />}
                        </div>
                        <div class='flex-grow-1 pl-3' style='line-height: 1.2em;'>
                            <a class='stretched-link' href={`/teams/${team.id}`}>
                                <h5 class='mb-1' style='line-height: 1.0em;'>{team.name}</h5>
                            </a>
                            <div>
                                {teamType && <span class='badge badge-pill badge-secondary'>{teamType}</span>}
                                {teamAmbition && <span class='badge badge-pill badge-secondary ml-2'>{teamAmbition}</span>}
                            </div>
                            <small class='text-muted'>
                                {team && team.dogs && <span class=''><i class='fas fa-dog' /> {team.dogs.length}</span>} 
                                {team && team.members && <span class='ml-2'><i class='fas fa-user' /> {team.members.length}</span>}

                                {workoutSummary && workoutSummary.distanceKm > 0 && <span class='ml-2'><i class='fas fa-road' /> {util.format(workoutSummary.distanceKm, 0)}km</span>}
                                {workoutSummary && workoutSummary.elevation > 0 && <span class='ml-2'><i class='fas fa-mountain' /> {util.format(workoutSummary.elevation, 0)}m</span>}
                                {workoutSummary && workoutSummary.duration > 0 && <span class='ml-2'><i class='fas fa-clock' /> {util.secToHms(workoutSummary.duration)}</span>}
                                {workoutSummary && workoutSummary.speedAvg > 0 && <span class='ml-2'><i class='fas fa-tachometer-alt' /> {util.format(workoutSummary.speedAvg, 1)}km/t</span>}
                            </small>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Team;
